body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body{
  font-family:Arial, Helvetica, sans-serif;
  margin: 0px;
}
a{
  text-decoration: none;
}
h4{
  margin: 5px;
  background-color: green;
  padding: 5px;
  -webkit-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
}
h1{
  background-color: steelblue ;
  color:white;
  padding: 10px;
  max-width: 350px;
  margin: 20px auto;
  border-radius: 10px; 
  text-align: center; 
}
.sobre-nosotros{
  text-align: center;
  max-width: 80%;
  margin: auto;
}

/* Formularios -Iniciar sesion - Registrarse */
.menu-container{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 25px;
  -webkit-box-shadow: 0px 8px 10px 1px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 8px 10px 1px rgba(0,0,0,0.75);
  box-shadow: 0px 8px 10px 1px rgba(0,0,0,0.75);
}
@media (max-width: 500px){
  .menu-container{
    display: flex;
    flex-direction: column;
      
  }
}
@media (max-width: 500px){
  .container-logo{
    display: flex;
    flex-direction: column;  
  }
}

@media (max-width: 500px){
  .container-menu{
    display: flex;
    flex-direction: column;
      
  }
  strong{
    display: none;
  }
}
.logo{
  width: 75px;
}

.menu-enlasses{
  text-align: center;
  color: navy;
  font-weight: bold;
  padding: 10px;
}
.menu-enlasses:hover{
  background-color: navy;
  color: white;
  font-weight: bold;
  border-radius: 10px;

}
@media (max-width: 500px){
  .menu-enlasses{
    padding-bottom: 10px;
    display: block;
    padding: 10px 10px;  
    width: 350px;
  }    
  
}
.activado{
  background-color: navy;
  color: white;
  font-weight: bold;
  border-radius: 10px;
  border: 1px solid orangered;
}
.container-principal{
  max-width: 90%;
  margin: auto;
  text-align: center;
  gap: 20px;
}
.formulario{
  padding: 20px;
  width: 40%;
  margin: auto;
  display:flex;
  flex-direction: column;
  border: 1px solid aqua;
  margin-top: 20px;
  border-radius: 15px;
  -webkit-box-shadow: 0px 8px 10px 5px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 8px 10px 5px rgba(0,0,0,0.75);
  box-shadow: 0px 8px 10px 5px rgba(0,0,0,0.75);
}
@media (max-width: 700px){
  .formulario{
    width: 85%;  
  }
}
.label{
  margin-top: 15px;
  width: 135px;
  margin-left: 15px;
  background-color: white;
  z-index: 100;
}
.label-1{
  margin-top: 15px;
  width: 60px;
  margin-left: 15px;
  background-color: white;
  z-index: 100;
  font-size: 15px;
}
.label-2{
  margin-top: 15px;
  width: 80px;
  margin-left: 15px;
  background-color: white;
  z-index: 100;
  font-size: 15px;

}
.input{
  margin-top: -8px;
  padding: 5px;
  border-radius: 10px;
  text-align: center;
  font-size: 15px;
}
.boton-formulario{
  margin: 10px 0px;
  padding: 7px;
  text-decoration: none;
  font-weight: bold;
  text-transform:uppercase;
  background-color:lightseagreen;
  border: none;
  border-radius: 10px;
  color: white
}
.boton-formulario:hover{
  background-color: green;
  cursor: pointer;
}
.demo{
  background-color: green;
  padding: 8px;
  border-radius: 10px;
}
/* Pagina Home*/
.container-logo{
  display: flex;
  align-items: center;

}
.container-peronajes{
  border: 1px solid lightseagreen;
  padding: 15px;
  border-radius: 10px;
  text-align: center;
}
.container-peronajes:hover{
  -webkit-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
-moz-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
}
.imagen-personajes{
  width: 90%;
  -webkit-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);

}
.datos-personajes{
  margin: 0px;
  padding: 5px;
}
.episodios{
  background-color: royalblue;
}
.container-principal-personajes{
  display: grid;
  grid-template-columns: repeat(5,1fr);
  gap: 10px;
  border: 2px solid blue;
  border-radius: 15px;
  padding: 20px;
}
@media (max-width: 600px){
  .container-principal-personajes{
      grid-template-columns: repeat(2,1fr);
  }
}
@media (max-width: 500px){
  .nav{
    display: flex;
    flex-direction: column;
    display: inline-flex;  
  }
}

.container-episodios{
  border: 2px solid cyan;
  margin-bottom: 10px;
  border-radius: 10px;
  
}
.container-episodios:hover{
  -webkit-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  -moz-box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
  box-shadow: 7px 8px 10px -1px rgba(0,0,0,0.75);
}

.logo-episodio{
  width: 75px;
}
/* Contenedor PersonajesPage*/
.ico-lupa{
  width: 20px;
}
@media (max-width: 500px){
  .ico-lupa{
    display: none;
    
  }
}
.input-buscar{
  width: 350px;
  margin: 10px 10px;
  padding: 10px;
  border-radius: 10px;
}
.boton-buscar{
  margin: 0px 0px;
  background-color: steelblue;
  padding: 10px;
  border: none;
  font-weight: bold;
  
}
.boton-buscar:hover{
  background-color: green;
  cursor: pointer;
}
.buscar-1{
  border-radius: 200px 0px 0px 200px;
