}
.container-buscar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 500px){
  .container-buscar{
    display: flex;
    flex-direction: column;
    display: inline-flex;  
  }
}
.container-lupa{
  align-items: center;
}
.buscar{
  font-weight: bold;
  text-transform: uppercase;
  margin: 0px 10px;
